import React from 'react';
import ScrollyVideo from 'scrolly-video/dist/ScrollyVideo.cjs.jsx';
import vedio from './videos/tech.mp4';
const App = () => {
  return (
    <div>
      <div style={{ height: '10vh', backgroundColor: 'black' }}>
        <h1>Scroll Down to Play the Video</h1>
      </div>

      <ScrollyVideo
        src={vedio}
        style={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '20vh' }}
      />

      <div style={{ height: '20vh', backgroundColor: 'black' }}>
        <h2>Keep Scrolling...</h2>
      </div>
    </div>
  );
};

export default App;
