import './App.css';
import Lenis from '@studio-freight/lenis';
import { useEffect, useRef } from 'react';
import gsap from 'gsap';
import ScrollTrigger from 'gsap/dist/ScrollTrigger';
import Tech from './tech';

function App() {
  const imageRef = useRef();
  const imageContainerRef = useRef();

  // GSAP
  gsap.registerPlugin(ScrollTrigger);

  useEffect(() => {
    gsap.to(imageRef.current, {
      xPercent: -100, // Adjust this value based on the width of your lengthy image
      ease: 'none',
      scrollTrigger: {
        trigger: imageContainerRef.current,
        scrub: 1,
        end: '+=' + imageContainerRef.current.offsetWidth,
        pin: true,
      },
    });

    return () => {
      ScrollTrigger.getAll().forEach((st) => st.kill());
    };
  }, []);

  // Lenis smooth scroll
  const lenis = new Lenis();
  lenis.on('scroll', () => {});

  function raf(time) {
    lenis.raf(time);
    requestAnimationFrame(raf);
  }
  requestAnimationFrame(raf);

  return (
    <main className="bg-[#101010] w-full overflow-x-hidden">
      <section className="min-h-screen flex justify-center items-center">
        <h1 className="font-bold text-8xl text-white">Scroll down</h1>
      </section>
      <section
        className="min-h-screen flex flex-nowrap items-center px-20"
        ref={imageContainerRef}
        style={{ width: '100vw' }}
      >
        <div className="w-[100vh] shrink-0 rounded-3xl overflow-hidden" ref={imageRef}>
          <img src={`/img/long-image.jpg`} className="long-image" alt="Long" />
        </div>
      </section>
      <section className="w-full min-h-screen"></section>
      <Tech/>
    </main>
  );
}

export default App;
